<template>
    <div class="profile-wrapper" v-show="user" v-bind:class="[user ? 'slideUp' : '' , isClosing ? 'slideDown' : '']">
        <div class="name-box">
            <span v-if="client._id != user._id">
                {{user.name}}
            </span>
            <span v-if="client._id === user._id">
                {{client.name}}
            </span>

            <button @click="closeProfile">
                <i class="fa fa-window-close fa-lg" aria-hidden="true"></i>
            </button>

        </div>
        <div class="img-box" v-if="!isEdit">
            <span v-if="!user.image"><i style="margin-top: 15px" class="fa fa-circle-o-notch fa-spin fa-lg fa-fw"></i></span>
            <img v-if="user._id != client._id && user.image != 'undefined.undefined'" :src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_80/' + user.image" alt="">
            <img v-if="user._id === client._id && client.image != 'undefined.undefined'" :src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_80/' + client.image">
            <img v-if="user.image === 'undefined.undefined' || client.image === 'undefined.undefined'" src="http://res.cloudinary.com/dxwtipc8n/image/upload/v1509920658/male-avatar_illsec.svg" alt="" >
        </div>
        <div class="details-box" v-if="!isEdit">
            <div class="description">
                <small>DESCRIPTION</small>
                <br>
                <br> 
                <pre class="user-description">
                    {{user.description}}    
                </pre> 
            </div>
            <br>
            <br>
            <div style="border-bottom: 1px solid #eee"></div>

            <div class="other-details">
                <small>COURSE</small>
                <br>
                <br>
                <span style="text-transform : uppercase">{{user.course}}</span>
                <br>
                <br>
                <small>GENDER</small>
                <br>
                <br>
                <span style="text-transform : uppercase">{{user.gender}}</span>
                <br>
                <br>
                <small>INTERESTS</small>
                <br>
                <br>
                <span>{{user.interests}}</span>
            </div>
            <br>
            <br>
            <br>
            <br>
            <br>
            <br>
            <button @click="sendMessage(user._id , user.image)" class="btn-send" v-if="client._id != user._id  && isMatched">
            <i class="fa fa-paper-plane fa-lg" aria-hidden="true"></i> Message
        </button>
        <button @click="editProfile"  class="btn-send" v-if="client._id === user._id && !isEdit">   
            <i class="fa fa-file-text fa-lg" aria-hidden="true"></i> Edit
        </button>
        <br>
        <br>
        <button @click="deactivate"  class="btn-send"  style="background-color: red;color : #fff;" v-if="client._id === user._id && !isEdit">   
           Deactivate my Account
        </button>

        <div style="height: 200px;"></div>

        </div>
        <div class="details" v-if="isEdit">
     
            <label for="pic-upload" class="pic-upload">
                 <img src="https://res.cloudinary.com/dxwtipc8n/image/upload/v1509920658/male-avatar_illsec.svg" alt="" v-show="image === '' && !client.image">
                <img :src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_80/' + client.image" alt="" v-show="image === ''">
                <img :src="image" v-show="image != ''">
                <b> click avatar to upload a picture</b>
            </label>
            <input id="pic-upload" type="file" @change="onFileChange" accept="image/*;capture=camera" style="display:none">
            <label for="name" style="text-transform:capitalize">Name</label>
            <input type="text" id="name" v-model="client.name">
            <label for="gender">Gender</label>
            <select name="gender" id="gender" v-model="client.gender">
                <option value="male">male</option>
                <option value="female">female</option>

            </select>
            <label for="course">Course</label>
            <input type="text" id="course" style="text-transform:uppercase" v-model="client.course">
            <label for="description">Description</label>
            <textarea name="description" id="description" cols="30" rows="10" placeholder="tell me something about yourself" v-model="client.description"></textarea>
            <label for="interested">Interested in:</label>
            <select name="interested" id="" v-model="client.interests">
                <option value="men">men</option>
                <option value="women">women</option>
                <option value="both">both</option>
            </select>
            <button @click="saveEdit" v-if="!isEditSaving">
                Save
            </button>
            <button @click="cancelEdit" style="background-color : yellow;color : #333" v-if="!isEditSaving">
                Cancel
            </button>
             <div style="height: 200px;"></div>
            <div v-if="isEditSaving" style="width:100%;text-align:center;position:absolute;bottom : 80px;left: -20px;">
                    <i class="fa fa-circle-o-notch fa-spin fa-3x fa-fw"></i>
                    <span class="sr-only">Loading...</span>
                </div>
        </div>
        <div v-if="isShowDeactivate" class="deactivate-account-wrapper">
           
            <div class="deactivate-container">
               <h4>
                Are you sure you want to deactivate your account?
               </h4>
               <small>
                   all of your data will be deleted and can no longer use this account
               </small>
            </div>
            <div class="deactivate-btns">
                <button @click="deactivateConfirm">Yes</button>
                <button @click="cancelDeactivate">No</button>
            </div>
        </div>
        
    </div>
</template>
<script>
   import {
        mapGetters
    } from 'vuex';
    import {
        Bus
    } from '../main'
    export default {
        name: 'profile',
        data() {
            return {
                isClosing: false,
                user: {},               
                isMatched: false,
                isEdit : false,
                image : '',
                isEditSaving : false,
                isShowDeactivate : false,
            }
        },
       computed: {
            ...mapGetters({
               
                client: 'user',
              
            }),

        },
        mounted: function () {
            Bus.$on('deactivateSuccess' , ()=>{
                this.$router.push('/login')
            })
            Bus.$on('editSuccess' , ()=>{
                this.isEdit = false
                this.isEditSaving = false
                this.image = ''
            })
            Bus.$on('openProfile', user => {
               
                this.user = user
                this.isClosing = false
                this.isMatched = false
                for (var i = 0; i < this.$store.state.matched.length; i++) {

                    if (!this.isMatched) {
                        if (this.$store.state.matched[i]._id == this.user._id) {

                            this.isMatched = true
                        }
                    }

                }

                setTimeout(()=>{
                     this.$el.scrollTop = 0
                } , 40)
            })

        },
        methods: {
            deactivateConfirm(){
                console.log(this.client._id)
                this.$socket.emit('deactivateAccount' , this.client._id)
            },
            deactivate(){
                this.isShowDeactivate = true
            },
            cancelDeactivate(){
                this.isShowDeactivate = false
            },
            saveEdit(){
                this.$socket.emit('saveEdit' , this.image , this.client)
                this.isEditSaving = true

            },
            cancelEdit(){
                this.$socket.emit('getUser' , this.client._id);
                this.isEdit = false
                this.$el.scrollTop = 0
            },
            onFileChange(e) {
                var files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                this.createImage(files[0]);

                this.imageFacade = files[0].name;

            },
            createImage(file) {
                var image = new Image();
                var reader = new FileReader();
                var vm = this;

                reader.onload = (e) => {
                    vm.image = e.target.result;
                };
                reader.readAsDataURL(file);

            },
            editProfile : function(){
                this.isEdit = true
                this.$el.scrollTop = 0

            },
            closeProfile: function () {
                this.isClosing = true
                this.isEdit = false
                this.isShowDeactivate = false

                 this.$socket.emit('getUser' , this.client._id);
                setTimeout(function () {
                    Bus.$emit('closeProfile');
                }, 400);
                 

            },
            sendMessage: (userId, userImage) => {
                
                this.isClosing = true
                
                setTimeout(function () {
                   
                    Bus.$emit('closeProfile');
                    Bus.$emit('closeProfiles');
                    Bus.$emit('hasReadMessages' , userId , userImage)
                    Bus.$emit('openMessage')
                    Bus.$emit('getMessages1', userId, userImage)
                }, 400)



            },
        }
    }
</script>
<style scoped>
    .profile-wrapper {
        position: fixed;
        height: 100%;
        width: 100%;
        overflow-y: scroll;
        overflow-x: hidden;
        z-index: 1100;
        top: 100%;
        background-color: #fff;
    }

    .img-box {
        display: block;
        width: 100%;
    }

    .img-box img {
        width: 100%;
    }

    .name-box {
        display: block;
        width: 100%;
        padding-top: 10px;
        padding-bottom: 10px;
        margin-left: 10px;
        font-size: 1.5em;
        text-transform: capitalize;
        font-weight: bold;
        letter-spacing: 1px;
        color: #f4a142;
    }

    .name-box button {
        background-color: transparent;
        border: none;
        position: absolute;
        right: 0;
        margin-right: 10px;
        top: 5px;
        font-size: 1em;
        color: #f4a142;
    }

    .description {
        padding-top: 10px;
        padding-left: 20px;
        width: 100%;
    }

    .other-details {
        padding-top: 10px;
        padding-left: 20px;
        width: 100%;
    }

    .btn-send {
        width: 80%;
        padding: 10px;
        display: block;
        margin: 0 auto;
        background-color: #f4a142;
        border: none;
        font-size: 1.2em;
        padding: 20px;
        color: #8c591e;
        box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5)
    }

    .slideUp {
        top: 100%;
        animation-name: slideUp;
        animation-duration: 0.2s;
        animation-fill-mode: forwards;
    }

    .slideDown {

        animation-name: slideDown;
        animation-duration: 0.4s;
    }
    .user-description{
        font-size: 1em;
        font-family: 'Raleway', sans-serif;
      
        padding: 0;
         white-space: pre-line
        
    }
    small {
        font-size: 0.7em;
        letter-spacing: 1px;
        color: #f4a142;
    }
    .details {
        padding: 20px;
        position: relative;
        width: 100%;
        padding-bottom: 100px;
        background-color: #fff;
    }

    .details label {
        display: block;
        font-size: 0.8em;
        color: #8f8f8f;
        margin-top: 10px;
    }

    .details input {
        display: block;
        margin-top: 5px;
        width: 90%;

        font-size: 1.5em;
        outline: none;
        border: none;
        border-bottom: 1px solid #999;
    }

    .details textarea {

        margin-top: 50px;
        width: 90%;
        font-size: 1em;
    }

    select {
        display: block;
        margin-top: 50px;
        width: 90%;
        text-transform: capitalize;
        font-size: 1em;
        outline: none;
        border: none;
        border-bottom: 1px solid #999;
    }

    .details button {
        font-size: 1em;
        display: block;
        margin-top: 20px;
        width: 90%;
        height: 50px;
        background-color: #31ad0f;
        color: #fff;
        font-weight: bold;
        outline: none;
        border: none;
    }
     .pic-upload {
        width: 100%;
        
      
       
    }

    .pic-upload img {
        width: 100%;
        display: block;
        margin: 0 auto;
        margin-left: -20px;
        
        border: 1px solid #999;
    }

    .pic-upload b {
         margin-left: -20px;
         
        
        top: -35px;
        left: -10px;
         color :#fff;
        position: relative;
        bottom : 0;
        text-align: center;
        font-size: 1.2em;
        padding-top: 10px;
        padding-bottom: 10px;
        vertical-align: middle;
        background-color: rgba(0,0,0,0.6);
        display: block;
    }
    .deactivate-account-wrapper{
        top: 0;
        position:fixed;
        z-index: 1500;
        font-size: 1em;
        width: 100%;
        height: 100%;
        background-color :#fff;
       
        padding-top: 100px;
    }
    .deactivate-container{
        font-size : 1.2em;
        text-align : center;
    }
    .deactivate-container small{
        color :#888;
    }
    .deactivate-btns{
        margin-top: 90px;
    }
    .deactivate-btns button{
        display:block;
        width: 50%;
        padding: 10px;
        font-size: 1.2em;
       margin : 0 auto;
        margin-top: 20px;
    }
    @keyframes slideUp {
        from {
            top: 100%
        }
        to {
            top: 0
        }
    }

    @keyframes slideDown {
        from {
            top: 0%
        }
        to {
            top: 100%
        }
    }
</style>



