<template>
    <div class="message-wrapper">
        <div class="message-container" :class="[closing ? 'slidedown' : 'slideUp']">
            <div class="nav-menu" :class="[closing ? 'fadeOut' : 'fadeIn']">
                <i class="fa fa-envelope  fa-lg" aria-hidden="true"></i>
                <span style="font-weight:bold">Messages</span>
                <i @click="closeMessage" style="position:absolute;right: 10%;" class="fa fa-times fa-lg" aria-hidden="true"></i>

            </div>
            <div class="matched-container" :class="[closing ? 'match-fadeOut' : 'match-fadeIn']">
                <span class="matched-label">MATCHED</span>
                <span class="match-waiting" v-if="matched.length === 0 ">
                    <i style="margin-top: 15px" class="fa fa-circle-o-notch fa-spin fa-lg fa-fw"></i>
                </span>
                <avatar :key="user._id" v-for="user in matched" :user="user"></avatar>
            </div>
            <div v-if="isMessageLoading" class="message-loader">
                <i style="margin-top: 15px" class="fa fa-circle-o-notch fa-spin fa-2x fa-fw"></i>
            </div>
            <div v-if="!isMessageLoading" class="chat-box" :class="[closing ? 'chat-fadeOut' : 'chat-fadeIn']">
                <div ref="refresher" v-if="refresh" class="touch-me">
                    <div class="get-prev">
                        <span v-if="!isGettingPrevious && !hasNoPrevMsg" @click="getPreviousMessages"> Load previous Message</span>
                        <span v-if="isGettingPrevious">
                            <i class="fa fa-circle-o-notch fa-spin fa-lg fa-fw"></i>
                        </span>
                        <span v-if="hasNoPrevMsg">No More Messages</span>
                    </div>
                </div>

                <div style="height: 250px;"></div>

                <div v-if="messages.length > 0" class="match-message day" style="">
                    {{messages[0].day}}
                </div>
               


                <singlemessage v-bind:key="message._id" :user="user" :message="message" :keyId="key" :messages="messages" :messageAvatar="messageAvatar"
                    v-for="(message , key , index) in messages"></singlemessage>







                <div v-if="showChatInput" class="chat-input" :class="[closing ? 'input-fadeOut' : 'input-fadeIn']">
                    <input type="text" v-model="message">
                    <button @click="sendMessage(message)">
                        <i class="fa fa-paper-plane fa-lg" aria-hidden="true"></i>
                    </button>
                </div>
            </div>


        </div>


    </div>
</template>
<script>
    import {
        ObserveVisibility
    } from 'vue-observe-visibility'
    import {
        Bus
    } from '../main'
    import {
        mapGetters
    } from 'vuex';
    import avatar from './avatar.vue'
    import singlemessage from './singlemessage.vue'

    export default {
        name: 'messages',
        data() {
            return {
                closing: true,
                scrollPoint: '',
                isChatBoxOpen: false,
                messageAvatar: '',
                message: '',
                userId: '',
                oldUser: '',
                avatarUser: '',
                messageCounter: 0,
                refresh: false,
                messageLength: 0,
                isGettingPrevious: false,
                hasNoPrevMsg: false,
                isMessageLoading: false,
                showChatInput: false
            }
        },
        components: {
            avatar,
            singlemessage
        },
        methods: {
            getPreviousMessages() {


                this.$socket.emit('getPreviousMessages', this.$store.state.user._id, this.userId, 0, this.messageLength)

                this.isGettingPrevious = true
            },
            closeMessage() {

                this.closing = true
                this.oldUser = ''
                this.showChatInput = false
                setTimeout(() => {
                    Bus.$emit('closeMessage')
                }, 500)
            },
            sendMessage(msg) {
                this.message = ''
                if (msg) {
                    this.$socket.emit('sendMessage', this.$store.state.user._id, this.userId, msg);
                }

            },
            checkIfUserAgain: function (user) {
                if (this.avatarUser === user) {
                    return false
                } else {
                    this.avatarUser = user
                    return true
                }
            },


            handleScroll() {

                var elem = this.$el.children[0].children[2]
                if (elem.scrollTop === 0) {

                    this.refresh = true
                    setTimeout(() => {

                    }, 200)

                }

                if (elem.scrollTop >= 100) {

                    this.refresh = false

                }


            }



        },
        computed: {
            ...mapGetters({
                matched: 'matched',
                messages: 'messages',
                user: 'user',
                newMessages: 'newMessages'
            }),

        },

        mounted: function () {

            this.$el.children[0].children[2].addEventListener('scroll', this.handleScroll)
            Bus.$on('messageLoading', () => {
                this.showChatInput = false;
                this.isMessageLoading = true
            })
            Bus.$on('openMessage', () => {
                this.closing = false
            })

            Bus.$on('messages', () => {
                this.refresh = false
                this.hasNoPrevMsg = false
                this.oldUser = this.userId
                this.isMessageLoading = false;
                this.showChatInput = true;
                setTimeout(() => {
                    var elem = this.$el.children[0].children[2]
                    elem.scrollTo(0, elem.scrollHeight);
                }, 50)

            })
            Bus.$on('messageSuccess', () => {

                var elem = this.$el.children[0].children[2]
                elem.scrollTop = elem.scrollHeight;

            })
            Bus.$on('prevMessageSuccess', () => {
                this.isGettingPrevious = false

                this.hasNoPrevMsg = true


            })



            Bus.$on('getMessages1', (userId, userImg) => {


                if (userId != this.oldUser) {

                    this.$store.state.messages = []
                    this.userId = userId
                    this.$socket.emit('getMessages', this.$store.state.user._id, userId)
                    this.messageAvatar = userImg
                    this.oldUser = userId


                } else {
                    this.isMessageLoading = false;
                    this.oldUser = userId
                    this.showChatInput = true

                }

                this.$store.state.chatActive = userId
            })







        }
    }
</script>
<style scoped>
    @import url('https://fonts.googleapis.com/css?family=Roboto');
    .message-container {
        position: fixed;
        z-index: 1200;
        width: 100%;
        height: 100%;
        top: 100%;
        background-color: #fff;
        font-size: 1em;
        overflow-y: scroll;
        overflow-x: hidden;
        box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);
    }

    .nav-menu {
        position: fixed;
        top: 100%;
        width: 100%;
        opacity: 0;
        color: #e02100;
        padding-top: 10px;
        padding-left: 20px;
        height: 40px;
        z-index: 30;
        background-color: #fff;
        border-bottom: 1px solid #e02100;
    }

    .matched-container {
        z-index: 20;
        background-color: #fff;
        position: fixed;
        top: 50px;
        padding-top: 10px;
        height: 70px;
        width: 100%;
        white-space: nowrap;
        overflow: auto;
        box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);
    }

    .input-fadeIn {
        bottom: 0;
        transition: all 0.3s ease-in-out;
        transition-delay: 0.3s;
    }

    .input-fadeOut {
        margin-bottom: 500%;
        transition: all 0.1s ease-in-out;
        transition-delay: 0;
    }

    .chat-fadeIn {
        opacity: 1;
        transition: all 0.3s ease-in-out;
        transition-delay: 0.5s;
    }

    .chat-fadeOut {
        opacity: 0;
        transition: all 0.1s ease-in-out;
        transition-delay: 0;
    }

    .slideUp {
        transition: all 0.3s ease-in-out;
        top: 0%;
    }

    .slidedown {
        transition: all 0.3s ease-in-out;
        transition-delay: 0.1s;
        top: 100%;
    }

    .match-fadeIn {

        transition: all 0.3s ease-in-out;
        transition-delay: 0.3s;
    }

    .match-fadeOut {
        opacity: 0;
        top: 100%;
        transition: all 0.1s ease-in-out;
        transition-delay: 0;
    }

    .fadeIn {
        opacity: 1;
        top: 0%;
        transition: all 0.3s ease-in-out;
        transition-delay: 0.3s;
    }

    .fadeOut {
        opacity: 0;
        top: 100%;
        transition: all 0.1s ease-in-out;
        transition-delay: 0;
    }

    .matched-label {
        position: fixed;
        font-size: 0.1em;
        font-weight: bolder;
        letter-spacing: 8px;

        left: 10px;
        color: #e02100;
    }

    .chat-box {

        position: absolute;
        width: 99%;
        bottom: 80px;
        z-index: 0;
        height: 100%;
        color: #fff;
        margin-top: 200px;
        overflow-y: scroll;
        overflow-x: hidden;
        font-size: 1em;
        padding: 5px;
    }

    .chat-input {
        position: fixed;
        bottom: 0;
        width: 100%;
        border-top: 1px solid #eee;
        background-color: #fff;
        padding-top: 10px;
        height: 100%;
        padding-bottom: 10px;
        left: 0;
        height: 30px;
        padding-left: 5px;
    }

    .chat-input input {
        background-color: transparent;
        border: none;
        border-bottom: 1px solid #888;
        width: 85%;
        font-size: 1.2em;
    }

    .chat-input input:focus {
        outline: none;
    }

    .chat-input button {
        background-color: transparent;
        border: none;
        color: #e02100;
    }

    .border-red {
        border: 4px solid #e02100;
    }

    .day {
        color: #aaa;
        text-align: center;
        text-transform: uppercase;
        font-weight: bolder;
        font-size: 0.8em;
        padding-bottom: 20px;
        margin-left: -5px;
        padding-left: 10px;
        letter-spacing: 2px;
        border-bottom: 1px solid #eee;
    }

    .touch-me {

        width: 100%;
        position: absolute;
        top: 90px;
        height: 250px;
        z-index: 1300;
    }

    .get-prev {
        position: relative;
        width: 200px;
        height: 20px;
        border-radius: 40px;
        padding: 10px;
        left: 50%;
        top: 180px;
        margin-top: -10px;
        margin-left: -120px;
        color: #333;
        font-size: 0.9em;
        border: none;
        background-color: #eee;
        text-align: center;
        animation-name: slidedown;
        animation-duration: 0.4s;
        animation-fill-mode: forwards;
    }

    .match-waiting {
        display: inline-block;
        background-color: #eee;
        border-radius: 50%;
        text-align: center;
        position: relative;
        height: 50px;
        width: 50px;
        overflow: hidden;
        margin-top: 10px;
        font-size: 1.2em;
        margin-left: 20px;
    }

    .message-loader {
        position: absolute;
        bottom: 100px;
        text-align: center;
        width: 100%;
        height: 100%;
        margin: 0 auto;
        top: 50%;
        font-size: 1.5em;
    }
 
    @keyframes slidedown {
        0% {
            top: 0
        }
        100% {
            top: 180px
        }
    }
</style>



